<template>
  <div class="user">
    <div class="block-quote">
      <el-form :inline="true">
        <el-form-item label=":">
          <el-cascader
            v-model="value"
            :options="areaArr"
            :props="{ value: 'name', label: 'name' }"
            placeholder=""
            filterable
            @change="change"
          ></el-cascader>
        </el-form-item>
        <el-form-item label=":">
          <el-cascader
            v-model="value1"
            :options="areaArr"
            :props="{ value: 'name', label: 'name' }"
            placeholder=""
            filterable
            @change="change1"
          ></el-cascader>
          <el-form-item prop="officina_id">
            <el-select
              v-model="name"
              placeholder=":"
              style="width: 180px"
              clearable
            >
              <el-option
                v-for="item in list"
                :value="item.big_name"
                :key="item.big_name"
                :label="item.big_name"
              ></el-option>
            </el-select>
            <el-select
              v-model="officina_id"
              placeholder=""
              style="width: 180px"
              clearable
            >
              <el-option
                v-for="item in list1"
                :value="item.name"
                :key="item.id"
                :label="item.name"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form-item>

        <el-form-item label=":" prop="number">
          <el-input
            style="width: 200px"
            v-model="number"
            clearable
            placeholder=":"
          ></el-input>
        </el-form-item>
        <el-form-item label=":" prop="number">
          <el-input
            style="width: 200px"
            v-model="box_name"
            clearable
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="">{{ all }} </el-form-item>
        <el-form-item label="">{{ profit }} </el-form-item>
        <el-form-item style="float: right">
          <el-button type="primary" icon="el-icon-search" @click="searchinfo"
            ></el-button
          >
        </el-form-item>
      </el-form>
    </div>
    <page-table
      ref="dataTable"
      :data="userList"
      @changeCurrentPage="changeCurrent"
    >
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <span>{{
            (page.currentPage - 1) * page.pageSize + scope.$index + 1
          }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="box_number" label="" align="center">
      </el-table-column>
      <el-table-column prop="box_name" label="" align="center">
      </el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <span
            >{{ scope.row.eprovince }}-{{ scope.row.ecity }}-{{
              scope.row.earea
            }}--{{ scope.row.edetails }}</span
          >
        </template>
      </el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <span
            >{{ scope.row.dprovince }}-{{ scope.row.dcity }}-{{
              scope.row.darea
            }}--{{ scope.row.ddetails }}--{{ scope.row.dbigname }}--{{
              scope.row.dname
            }}</span
          >
        </template></el-table-column
      >

      <el-table-column label="" align="center" width="350">
        <template slot-scope="scope">
          <el-link
            type="primary"
            @click="showtable(scope.row)"
            style="margin-left: 10px"
            ></el-link
          >
        </template>
      </el-table-column>
    </page-table>
    <!--  -->
    <edit-data ref="editData" />
  </div>
</template>

<script>
import {
  officinalist,
  goodsAll,
  positiondeletet,
  positiondelete,
} from "@/request/api";
import { checkPermission } from "@/utils/permissions";
import pageTable from "@/components/pageTable.vue";
import { areaListData } from "@/utils/area";
import editData from "./components/editData.vue";

export default {
  name: "user",
  components: {
    pageTable,
    editData,
  },
  data() {
    return {
      box_uid: "",
      box_name: "",
      areaArr: [],
      number: "",
      value: "",
      value1: "",
      list: [],
      list1: [],
      profit: "",
      all: "",
      name: "",
      officina_id: "",
      province: "",
      city: "",
      area: "",
      eprovince: "",
      ecity: "",
      earea: "",
      userList: [], // 
      time: "",
      page: {
        //
        currentPage: 1, //
        pageSize: 10, //
        total: 0, //
      },
    };
  },
  watch: {
    time(newVal) {
      if (newVal == null) {
        this.time = [];
      }
    },
    name(newVal) {
      this.huolist1();
    },
  },
  created() {
    this.getUserList(); //
    this.setData(areaListData());
    this.areaArr = areaListData();
  },
  mounted() {},
  computed: {},
  methods: {
    huolist1() {
      let params = {
        //big_name 3
        token: sessionStorage.getItem("token"),
        big_name: this.name,
        province: this.province,
        city: this.city,
        area: this.area,
      };
      officinalist(params).then((res) => {
        this.list1 = res.data.data;
      });
    },

    setData(data) {
      data.map((item) => {
        item["value"] = item.code;
        item["label"] = item.name;
        if (item.children) {
          this.setData(item.children);
        }
      });
    },
    change(data) {
      this.eprovince = data[0];
      this.ecity = data[1];
      this.earea = data[2];
    },
    change1(data) {
      this.province = data[0];
      this.city = data[1];
      this.area = data[2];
      this.get();
    },
    get() {
      let params = {
        token: sessionStorage.getItem("token"),
        province: this.province,
        city: this.city,
        area: this.area,
      };
      officinalist(params).then((res) => {
        this.list = res.data.data;
      });
    },

    showtable(row) {
      let rowData = row;
      this.$refs.editData.show(JSON.parse(JSON.stringify(rowData)));
    },
    checkPermission,
    // 
    changeCurrent(page, size) {
      this.page.currentPage = page;
      this.page.pageSize = size;
      this.getUserList();
    },
    searchinfo() {
      this.page.currentPage = 1;
      let token = sessionStorage.getItem("token");
      this.token = token;
      let params = {
        page: 1,
        limit: this.page.pageSize,
        token: sessionStorage.getItem("token"),
        box_number: this.number,
        officina_name: this.officina_id,
        position_area: this.earea,
        box_name: this.box_name,
      };
      goodsAll(params).then((res) => {
        this.page.total = res.data.count;
        this.userList = res.data.data;
        this.all = res.data.allprice.all;
        this.profit = res.data.allprice.profit;
        this.$refs.dataTable.setPageInfo({
          total: this.page.total,
        });
      });
    },
    getUserList() {
      let token = sessionStorage.getItem("token");
      this.token = token;
      let params = {
        page: this.page.currentPage,

        limit: this.page.pageSize,
        token: sessionStorage.getItem("token"),
        box_number: this.number,
        officina_name: this.officina_id,
        position_area: this.earea,
        box_name: this.box_name,
      };
      goodsAll(params).then((res) => {
        this.page.total = res.data.count;
        this.all = res.data.allprice.all;
        this.profit = res.data.allprice.profit;
        this.userList = res.data.data;
        this.$refs.dataTable.setPageInfo({
          total: this.page.total,
        });
      });
    },
  },
};
</script>

<style lang="scss" scoped>
</style>
